.banner {
  color: white;
  object-fit: contain;
  height: 448px;
}

.banner__content {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}

.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.banner__description {
  width: 45rem;
  font-weight: 450;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.8rem;
  max-width: 360px;
  height: 80px;
}

.banner__button {
  cursor: pointer;
  color: white;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
}

.banner__button:hover {
  color: black;
  background-color: #e6e6e6;
  transition: all 0.2s;
}
.banner__gradient {
  height: 7.4rem;
  background-image: linear-gradient(rgba(17, 17, 17, 0), #111);
}
.banner__date {
  padding-bottom: 0px;
}
.banner__popularity {
  padding-bottom: 10px;
}
.heading {
  font-weight: bolder;
}
